@import '../helpers/variables';
@import '../helpers/mixins';

.modal_box,
.contacts_box {
  position: fixed;
  z-index: 8;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  .modal_main,
  .modal_thanks {
    background: $lightgrey url(image-url('/bg/modal.svg')) center no-repeat;
    //background-size: contain;
    position: relative;
    width: 654px;
    min-height: 510px;
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
    border-radius: 8px;
    padding: 29px 101px 10px;
    text-align: center;
    .modal_title,
    .thanks_header {
      font-family: $arial;
      font-style: normal;
      font-weight: bold;
      font-size: 32px;
      line-height: 37px;
      color: $black;
      margin-bottom: 32px;
    }
    .modal_subtitle,
    .thanks_text {
      font-family: $arial;
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 18px;
      color: $black;
      margin-bottom: 24px;
    }
    .submit {
      margin: auto;
    }
  }
  .wpcf7-form-control-wrap {
    display: block;
    margin-bottom: 6px;
  }
  input,
  textarea {
    background: $white;
    border: 1px solid rgba(73, 80, 95, 0.4);
    box-sizing: border-box;
    border-radius: 2px;
    width: 100%;
    padding: 10px 13px;
    font-family: $arial;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    &::placeholder,
    &:-ms-input-placeholder,
    &::-ms-input-placeholder { /* Microsoft Edge */
      font-family: $arial;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      color: #C4C4C4;
    }
    &:focus,
    &:active {
      border: 1px solid #00D2ED;
    }
  }
  .modal_thanks {
    display: flex;
    flex-direction: column;
    align-items: center;justify-content: center;
    .modal_check_success {
      background: $lightgrey url(image-url('/icons/modal_success.svg')) center no-repeat;
      background-size: cover;
      width: 84px;
      height: 84px;
      margin-bottom: 38px;
      cursor: pointer;
    }
  }
  .close_modal {
    position: absolute;
    top: 32px;
    right: 32px;
    padding: 0;
    cursor: pointer;
    width: 28px;
    height: 28px;
    background-image: url(image-url('/icons/close.svg')), linear-gradient(267.49deg, #19D7CF 4.21%, #3F95FC 100%);
    background-repeat: no-repeat;
    background-position: center;
    z-index: 1;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    border-radius: 2px;
    &:active {
      background-image: url(image-url('/icons/close.svg')), linear-gradient(267.49deg, #00D3CC 4.21%, #0074FF 100%);
      background-repeat: no-repeat;
      background-position: center;
    }
  }
}
.contacts_anchor {
  padding-top: 40px;
  margin-top: -40px;
}
section.contacts {
  background: $lightgrey url(image-url('/bg/hexa_bg.svg')) 100px 65px no-repeat;
  .container {
    width: 100%;
    max-width: 100%;
    background: url(image-url('/bg/hexa_bg.svg')) 380px 230px no-repeat no-repeat;
    padding-top: 18px;
    .contacts_box {
      position: relative;
      z-index: initial;
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      background: none;
      .modal_main,
      .modal_thanks {
        background: none;
        box-shadow: none;
        width: 540px;
        padding: 29px 0 0;
        .modal_title {
          margin-bottom: 40px;
        }
        .modal_subtitle {
          margin-bottom: 30px;
        }
        input,
        textarea {
          padding: 13px;
        }
        .submit {
          padding: 12px 30px;
          font-weight: bold;
          font-size: 16px;
          line-height: 18px;
        }
      }
    }
  }
}

